.callback-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
  text-align: center;
}

.callback-box {
  background-color: white;
  padding: 2rem;
  border-radius: 16px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.08);
  max-width: 500px;
  width: 90%;
}

.spinner {
  border: 4px solid rgba(167, 119, 227, 0.3);
  border-radius: 50%;
  border-top: 4px solid #a777e3;
  width: 40px;
  height: 40px;
  margin: 0 auto 20px;
  animation: spin 1s linear infinite;
}

.error-icon {
  font-size: 40px;
  margin-bottom: 20px;
}

.error-details {
  background-color: #f8f8f8;
  padding: 10px;
  margin: 15px 0;
  border-left: 3px solid #e74c3c;
  text-align: left;
  color: #666;
  border-radius: 4px;
}

.callback-help {
  margin-top: 20px;
  text-align: left;
  color: #666;
  line-height: 1.5;
}

.callback-uri {
  display: block;
  margin: 10px 0;
  background: #f0f0f0;
  padding: 8px;
  border-radius: 4px;
  word-break: break-all;
  font-family: monospace;
  font-size: 0.9em;
  border: 1px solid #ddd;
}

.retry-button {
  background-color: #a777e3;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 20px;
  cursor: pointer;
  margin-top: 20px;
  font-weight: 500;
  transition: background-color 0.3s ease;
}

.retry-button:hover {
  background-color: #8a5dcb;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
