.user-auth {
  margin-top: 1rem;
}

.spotify-login-btn {
  background: #1DB954; /* Spotify green */
  color: white;
  border: none;
  padding: 0.8rem 1.2rem;
  border-radius: 30px;
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: all 0.3s;
  box-shadow: 0 4px 12px rgba(29, 185, 84, 0.2);
}

.spotify-login-btn:hover {
  background: #1ED760;
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(29, 185, 84, 0.3);
}

.spotify-icon {
  margin-right: 8px;
  font-size: 1.2rem;
}

.user-profile {
  display: flex;
  align-items: center;
}

.user-avatar {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  margin-right: 10px;
  border: 2px solid white;
}

.user-info {
  display: flex;
  flex-direction: column;
}

.user-name {
  font-size: 0.9rem;
  font-weight: 600;
  color: white;
  margin-bottom: 4px;
}

.logout-btn {
  background: transparent;
  color: rgba(255, 255, 255, 0.8);
  border: 1px solid rgba(255, 255, 255, 0.4);
  padding: 2px 8px;
  border-radius: 12px;
  font-size: 0.7rem;
  cursor: pointer;
  transition: all 0.2s;
}

.logout-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  color: white;
}

.user-auth-loading {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 1s infinite linear;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}
